.statistics {
    margin-top: 40px;
    width: 500px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), 0 4px 6px rgba(0, 0, 0, 0.2);
    text-align: center;
    background: white;
}

.title {
    text-transform:uppercase;
    color: dimgray;
}

.stat_list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70px;
    padding: 0;
    list-style-type: none;
    font-size: 14px;
}

.item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: rgba(219, 219, 219, 0.521);
}